import { createRouter, createWebHashHistory } from 'vue-router'
import index from '../views'
import home from '../views/pages/home.vue'
import login from '../views/pages/login.vue'
import newsList from '@/views/pages/news/list'
import yonghuList from '@/views/pages/yonghu/list'
import yonghuDetail from '@/views/pages/yonghu/formModel'
import yonghuAdd from '@/views/pages/yonghu/formAdd'
import yonghuRegister from '@/views/pages/yonghu/register'
import yonghuCenter from '@/views/pages/yonghu/center'
import messagesList from '@/views/pages/messages/list'
import yujufenleiList from '@/views/pages/yujufenlei/list'
import yujufenleiDetail from '@/views/pages/yujufenlei/formModel'
import yujufenleiAdd from '@/views/pages/yujufenlei/formAdd'
import yujuxinxiList from '@/views/pages/yujuxinxi/list'
import yujuxinxiDetail from '@/views/pages/yujuxinxi/formModel'
import yujuxinxiAdd from '@/views/pages/yujuxinxi/formAdd'
import zulinxinxiList from '@/views/pages/zulinxinxi/list'
import zulinxinxiDetail from '@/views/pages/zulinxinxi/formModel'
import zulinxinxiAdd from '@/views/pages/zulinxinxi/formAdd'
import storeupList from '@/views/pages/storeup/list'
import guihaixinxiList from '@/views/pages/guihaixinxi/list'
import guihaixinxiDetail from '@/views/pages/guihaixinxi/formModel'
import guihaixinxiAdd from '@/views/pages/guihaixinxi/formAdd'

const routes = [{
		path: '/',
		redirect: '/index/home'
	},
	{
		path: '/index',
		component: index,
		children: [{
			path: 'home',
			component: home
		}
		, {
			path: 'newsList',
			component: newsList
		}
		, {
			path: 'yonghuList',
			component: yonghuList
		}, {
			path: 'yonghuDetail',
			component: yonghuDetail
		}, {
			path: 'yonghuAdd',
			component: yonghuAdd
		}
		, {
			path: 'yonghuCenter',
			component: yonghuCenter
		}
		, {
			path: 'messagesList',
			component: messagesList
		}
		, {
			path: 'yujufenleiList',
			component: yujufenleiList
		}, {
			path: 'yujufenleiDetail',
			component: yujufenleiDetail
		}, {
			path: 'yujufenleiAdd',
			component: yujufenleiAdd
		}
		, {
			path: 'yujuxinxiList',
			component: yujuxinxiList
		}, {
			path: 'yujuxinxiDetail',
			component: yujuxinxiDetail
		}, {
			path: 'yujuxinxiAdd',
			component: yujuxinxiAdd
		}
		, {
			path: 'zulinxinxiList',
			component: zulinxinxiList
		}, {
			path: 'zulinxinxiDetail',
			component: zulinxinxiDetail
		}, {
			path: 'zulinxinxiAdd',
			component: zulinxinxiAdd
		}
		, {
			path: 'storeupList',
			component: storeupList
		}
		, {
			path: 'guihaixinxiList',
			component: guihaixinxiList
		}, {
			path: 'guihaixinxiDetail',
			component: guihaixinxiDetail
		}, {
			path: 'guihaixinxiAdd',
			component: guihaixinxiAdd
		}
		]
	},
	{
		path: '/login',
		component: login
	}
	,{
		path: '/yonghuRegister',
		component: yonghuRegister
	}
]

const router = createRouter({
  history: createWebHashHistory(process.env.BASE_URL),
  routes
})

export default router
